/* icon */
.s3pIconV2 {
  display: inline-block;
  width: 1em;
  height: 1em;
  contain: strict;
  -webkit-box-sizing: content-box !important;
  box-sizing: content-box !important;
  overflow: hidden;
}

.sizeDefault{
  font-size: 26px;
}

.icon-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.load-icon {
  display: none;
}
.forClick {
  position: absolute;
  width: 1em;
  background: transparent;
  height: 1em;
}
.div-icon {
  position: relative;
}

.refreshIconPath {
  fill: none;
  stroke-linecap: round;
  stroke-miterlimit: 10;
  stroke-width: 42px;
}
.refreshIconPoly {
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 42px;
}
/* badge*/
.badgeStyle {
  background: black;
  border-radius: 50px;
  color: white;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: -6px;
}
.badge-large{
  font-size: 14px;
  min-width: 20px;
  height: 20px;
  top: -22px;
  padding: 1px;
}
.badge-default{
  font-size: 12px;
  min-width: 17px;
  height: 17px;
  top: -18px;
  padding: 1px;
}
.badge-small{
  font-size: 9px;
  min-width: 14px;
  height: 14px;
  top: -10px;
  right: -5px;
}

.icon-center.div-icon .badge-large{
  top: -6px;
}
.icon-center.div-icon .badge-default{
  top: -5px;
}
.icon-center.div-icon .badge-small{
  top: -4px;
}